$(document).ready(function () {
  $('#gbCalcForm').on('submit', function(e) {
    e.preventDefault();
    var data = $(this).serializeArray().reduce(function(obj, item) {
      obj[item.name] = item.value;
      return obj;
    }, {});

    var values = {
      'highestTalent': data["highestTalent"] ? parseInt(data["highestTalent"]) : 0,
      'totalTalent': data["totalTalent"] ? parseInt(data["totalTalent"]) : 0,
      'filledSlots': data["filledSlots"] ? parseInt(data["filledSlots"]) : 0,
      'totalOpponents': data["opponentsNumber"] ? parseInt(data["opponentsNumber"]) : 0,
      'lowestTalentModifier': data["lowestTalentModifier"] ? parseFloat(data["lowestTalentModifier"]) : 0,
      'modifierAdjustment': data["modifierAdjustment"] ? parseFloat(data["modifierAdjustment"]) : 0
    };

    var talents = [];
    for (var i = 1; i <= values['totalOpponents']; i++) {
      var prevTalent = 0;
      if (i > 1) {
        prevTalent = talents[talents.length-1];
      }
      var talent = calculateTalent(values, prevTalent, i);
      talents.push(talent);
    }
    printResults(talents);
  });

  function printResults(talents) {
    var tableElement = $('<table/>', {'class': 'table'})
    .append(
      $('<colgroup/>')
      .append(
        $('<col/>', {'span': '1', 'style': 'width: 25%;'})
      )
      .append(
        $('<col/>', {'span': '1', 'style': 'width: 75%;'})
      )
    )
    .append(
      $('<thead/>')
      .append(
        $('<tr/>')
        .append(
          $('<th/>', {'text': 'Opponent'})
        )
        .append(
          $('<th/>', {'text': 'Talent'})
        )
      )
    )
    .append(
      $('<tbody/>')
    );

    var tbodyElement = tableElement.find('tbody');
    talents.forEach(function(talent, i){
      tbodyElement.append(
        $('<tr/>')
        .append(
          $('<td/>', {'text': parseInt(i+1)})
        )
        .append(
          $('<td/>', {'text': parseInt(talent)})
        )
      );
    });

    $("#talentResults").empty();
    $("#talentResults").append(tableElement);
  }

  function calculateTalent(values, prevTalent, index) {
    var averageTalent = values['totalTalent'] / values['filledSlots'];
    var result = 0;

    if (index <= 1) {
      result = averageTalent * values['lowestTalentModifier'];
    } else {
      var highestTalentModifier = 1 + (values['highestTalent'] - averageTalent) / values['highestTalent'];
      var indexModifier = index / values['totalOpponents'];
      result = highestTalentModifier * indexModifier * (0.5 * (averageTalent + values['highestTalent'])) * values['modifierAdjustment'] + prevTalent;
    }

    return result;
  }
});



